*{padding:0px;margin:0px;box-sizing:border-box}body{background-image:url(../assets/t≈Ço.jpg)}.wrapper{width:100%;height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center}.wrapper .backgroundAdivce{background-color:rgba(255,0,0,.6);max-width:70%;text-align:center;margin-bottom:30px;padding:15px;border-radius:25px;border:2px solid #000}.wrapper .backgroundAdivce .advice{font-size:50px;color:#fff;font-family:"Anton",sans-serif;text-transform:uppercase;letter-spacing:1px}.wrapper .GlitchBtn,.wrapper .GlitchBtn:after{width:170px;height:76px;line-height:78px;font-size:20px;background:linear-gradient(45deg, transparent 5%, #ff013c 5%);border:0;color:#fff;letter-spacing:3px;box-shadow:6px 0px 0px #00e6f6;outline:rgba(0,0,0,0);position:relative;touch-action:manipulation;cursor:pointer}.wrapper .GlitchBtn:after{--slice-0: inset(50% 50% 50% 50%);--slice-1: inset(80% -6px 0 0);--slice-2: inset(50% -6px 30% 0);--slice-3: inset(10% -6px 85% 0);--slice-4: inset(40% -6px 43% 0);--slice-5: inset(80% -6px 5% 0);content:"Can be scary";display:block;position:absolute;top:0;left:0;right:0;bottom:0;background:linear-gradient(45deg, transparent 3%, #00e6f6 3%, #00e6f6 5%, #ff013c 5%);text-shadow:-3px -3px 0px #f8f005,3px 3px 0px #00e6f6;clip-path:var(--slice-0)}.wrapper .GlitchBtn:hover:after{animation:1s glitch;animation-timing-function:steps(2, end)}@keyframes glitch{0%{clip-path:var(--slice-1);transform:translate(-20px, -10px)}10%{clip-path:var(--slice-3);transform:translate(10px, 10px)}20%{clip-path:var(--slice-1);transform:translate(-10px, 10px)}30%{clip-path:var(--slice-3);transform:translate(0px, 5px)}40%{clip-path:var(--slice-2);transform:translate(-5px, 0px)}50%{clip-path:var(--slice-3);transform:translate(5px, 0px)}60%{clip-path:var(--slice-4);transform:translate(5px, 10px)}70%{clip-path:var(--slice-2);transform:translate(-10px, 10px)}80%{clip-path:var(--slice-5);transform:translate(20px, -10px)}90%{clip-path:var(--slice-1);transform:translate(-10px, 0px)}100%{clip-path:var(--slice-1);transform:translate(0)}}@media(min-width: 768px){.wrapper .GlitchBtn,.wrapper .GlitchBtn:after{width:200px;height:86px;line-height:88px}}@media(max-width: 1000px){.wrapper .backgroundAdivce .advice{font-size:25px;font-family:"Anton",sans-serif}.wrapper .GlitchBtn{font-size:15px}}